export const actionBanner = {
    props: {
        btnText1: String,
        btnText2: String,
        btnText3: String,
        articles: String
    },
    data: function() {
        return {
            current: '&#35;FairPayForNursing',
            active: false
        }
    },
    computed: {
        articleArray() {
            return JSON.parse(this.articles)
        }
    },
    template: /*html*/
        `<div class="action-banner__container">
            <div class="action-banner__images-wrapper">
                <div class="action-banner__images">
                    <transition name="fade">
                        <VideoViewer v-show="current == '&#35;FairPayForNursing'" id="action-banner-video" parent="#action-banner">
                            <template #opener>
                                <a class="video-viewer__video-link" href="#media"> 
                                    <slot name="image1"></slot>
                                </a>
                            </template>
                            <slot name="video1"></slot>
                        </VideoViewer>
                    </transition>

                    <transition name="fade">
                        <div v-show="current == 'Covid-19'">
                            <slot name="image2"></slot>
                        </div>
                    </transition>

                    <transition name="fade">
                        <div v-show="current == 'Nursing Support Workers&apos; Day'">
                            <slot name="image3"></slot>
                        </div>
                    </transition>
                </div>
            </div>

            <div class="action-banner__contents">
                <button
                    class="action-banner__accordion-btn"
                    :class="{ 'is-active': current === '&#35;FairPayForNursing'}"
                    :disabled="current === '&#35;FairPayForNursing'"
                    @click="current = '&#35;FairPayForNursing'"
                >
                    {{ articleArray[0].label }}
                </button>
                <slot name="accordion-section1" v-if="current === '&#35;FairPayForNursing'"></slot>
                    
                <button
                    class="action-banner__accordion-btn"
                    :class="{ 'is-active': current === 'Covid-19'}"
                    :disabled="current === 'Covid-19'"
                    @click="current = 'Covid-19'"
                >
                    {{ articleArray[1].label }}
                </button>
                <slot name="accordion-section2" v-if="current === 'Covid-19'"></slot>
                    
                <button
                    class="action-banner__accordion-btn"
                    :class="{ 'is-active': current === 'Nursing Support Workers&apos; Day'}"
                    :disabled="current === 'Nursing Support Workers&apos; Day'"
                    @click="current = 'Nursing Support Workers&apos; Day'"
                >
                    {{ articleArray[2].label }}
                </button>
                <slot
                    name="accordion-section3" v-if="current === 'Nursing Support Workers&apos; Day'"></slot>
            </div>

            <nav class="action-banner__tabs-bar">
                <button
                    class="action-banner__tab btn"
                    :class="{ 'is-active': current === '&#35;FairPayForNursing'}"
                    :disabled="current === '&#35;FairPayForNursing'"
                    @click="current = '&#35;FairPayForNursing'"
                >
                    {{ articleArray[0].label }}
                </button>
                <button
                    class="action-banner__tab btn"
                    :class="{ 'is-active': current === 'Covid-19'}"
                    :disabled="current === 'Covid-19'"
                    @click="current = 'Covid-19'"
                >
                    {{ articleArray[1].label }}
                </button>
                <button
                    class="action-banner__tab btn"
                    :class="{ 'is-active': current === 'Nursing Support Workers&apos; Day'}"
                    :disabled="current === 'Nursing Support Workers&apos; Day'"
                    @click="current = 'Nursing Support Workers&apos; Day'"
                >
                    {{ articleArray[2].label }}
                </button>
            </nav>
        </div>`
}